<template>
  <dashboard-panel :style-object="option.wrapperStyle" class="data-container">
    <div class="data-item nb">
      <div class="digital-title">NB表</div>
      <countup
        tag="div"
        v-if="countupnb"
        :start-val="nbData.oldNum"
        :end-val="nbData.num"
        :duration="nbData.durationValue"
        :decimals="nbData.decimalsValue"
        :roundsOff="false"
        :options="nbData.options"
        class="digital-remake-content"
      />
    </div>
    <div class="data-item cbl">
      <div class="digital-title">抄表率</div>
      <div class="digital-remake-content">
        <countup
          tag="div"
          v-if="countupcbl"
          :start-val="cblData.oldNum"
          :end-val="cblData.num"
          :duration="cblData.durationValue"
          :decimals="cblData.decimalsValue"
          :roundsOff="false"
          :options="cblData.options"
        />%
      </div>
    </div>
  </dashboard-panel>
</template>

<script>
import getData from "@/mixins/getData";
import Countup from "@/components/CountUp";

export default {
  name: "DataScreen",
  components: {
    Countup
  },
  mixins: [getData],
  props: {
    styleObject: {
      type: Object,
      default() {
        return {};
      }
    },
    option: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      countupnb: true,
      countupcbl: true,
      nbData: {
        num: 4733000,
        oldNum: 0,
        decimalsValue: 0,
        durationValue: 2,
        percentage: 0,
        // 翻牌器配置
        options: {
          format(val) {
            val = val + "";
            const valArray = val.split("");
            return valArray
              .map(str => {
                if (isNaN(+str)) {
                  return str;
                } else {
                  return `<span class="remake">${str}</span>`;
                }
              })
              .join("");
          }
        }
      },
      cblData: {
        num: 99.4,
        oldNum: 99.2,
        decimalsValue: 1,
        durationValue: 0.02,
        percentage: 0,
        // 翻牌器配置
        options: {
          format(val) {
            val = val + "";
            const valArray = val.split("");
            return valArray
              .map(str => {
                if (isNaN(+str)) {
                  return str;
                } else {
                  return `<span class="remake">${str}</span>`;
                }
              })
              .join("");
          }
        }
      }
    };
  },
  mounted() {
    this.changeNbData();
    this.changeCblData();
  },
  methods: {
    changeNbData(intervalTime = 2000) {
      this.nbDataTimer = setInterval(() => {
        this.countupnb = false; //因为文字添加了背景，不使用v-if则背景不重新渲染
        this.$nextTick(() => {
          this.countupnb = true;
        });
        this.nbData.oldNum = this.nbData.num;
        this.nbData.num = this.nbData.num + Math.round(Math.random() * 2);
      }, intervalTime);
    },
    changeCblData(intervalTime = 20000) {
      this.cblDataTimer = setInterval(() => {
        this.countupcbl = false; //因为文字添加了背景，不使用v-if则背景不重新渲染
        this.$nextTick(() => {
          this.countupcbl = true;
        });
        this.cblData.oldNum = this.cblData.num;
        this.cblData.num = this.cblData.num + Math.round((Math.random() * 0.7))/10;
        if(this.cblData.num >= 99.9){
          this.cblData.num = 99.2;
        }
      }, intervalTime);
    }
  },
  beforeDestoryed() {
    clearInterval(this.nbDataTimer);
    clearInterval(this.cblDataTimer);
    this.nbDataTimer = null;
    this.cblDataTimer = null;
  }
};
</script>

<style lang="scss" scoped>
.data-container {
  display: flex;
  color: #fff;
  line-height: 1;
  .nb {
    flex: 3;
  }
  .nbl {
    flex: 2;
  }
  .data-item {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    .digital-title {
      font-family:'DONGQINGW3';
      padding: 12px 50px;
      font-size: 24px;
      background: url("~@/assets/nb-lot/title-bg.svg");
      background-size: 100% 100%;
      margin-right: 16px;
      margin-bottom: 10px;
      white-space: nowrap;
    }
    .digital-remake-content {
      > div {
        display: inline;
      }
      font-size: 72px;
      display: inline-block;
      line-height: 82px;
      background: linear-gradient(
        180deg,
        rgba(47, 223, 255, 1) 0%,
        rgba(255, 255, 255, 1) 100%
      );
      -webkit-box-reflect: below -14px linear-gradient(180deg, rgba(
              47,
              223,
              255,
              0
            )
            0%, rgba(255, 255, 255, 0.32) 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
}
</style>
