export const BACKGROUND_COLOR = '#160486'

export default {
  data() {
    return {
      // 图表容器样式
      chartContainerStyle: {
        width: '100%',
        height: '25%',
        position: 'relative',
        paddingTop: this.subTitleFontSize + 6 + 'px',
        paddingBottom: '16px',
        boxSizing: 'border-box'
      },
      // 图表样式
      chartDefaultStyle: {
        top: '50px',
        bottom: '16px'
      },
      // 图表标题样式
      subTitleStyle: {
        fontSize: '24px',
        width: '100%',
        lineHeight: '50px',
        fontWeight: 500,
        color: '#fff',
        fontFamily: 'DONGQINGW6',
        boxSizing: 'border-box',
        backgroundSize: 'contain',
        backgroundRepeat: 'repeat-y',
        paddingLeft: '8px',
        paddingBottom:'5px',
        position: 'absolute',
        top: 0,
        left: 0,
        overflow: 'hidden',
        whiteSpace: 'nowrap',
        textOverflow: 'ellipsis'
      },
      winHeight: 1440,
      winWidth: 768,
      subTitleFontSize: 18
    }
  },
  mounted() {
    this.onResize()
    window.addEventListener('resize', this.onResize)
  },
  methods: {
    onResize() {
      this.winWidth = document.documentElement.clientWidth
      this.winHeight = document.documentElement.clientHeight
      this.subTitleFontSize = this.getSubTitleFontSize(this.winHeight)
      // this.subTitleStyle.fontSize = this.subTitleFontSize + 'px'
      // this.subTitleStyle.lineHeight = this.subTitleFontSize + 2 + 'px'
    },
    // 子菜单字号
    getSubTitleFontSize() {
      const ratio = 0.020
      return Math.round(this.winHeight * ratio)
    }
  },
  computed: {},
  beforeDestroy() {
    window.removeEventListener('resize', this.onResize)
  }

}
