<script>
    import CountUp from '@/js/countUp'

    export default {
        name : 'Countup',
        mounted() {
            this.$nextTick(() => {
                this._countup = new CountUp(this.$el, this.startVal, this.endVal, this.decimals, this.duration, this.options)
                if (this.start) {
                    this._countup.start()
                }
            })
        },
        props: {
            tag      : {
                type   : String,
                default: 'span'
            },
            start    : {
                type   : Boolean,
                default: true
            },
            startVal : {
                type   : [Number, String],
                default: 0
            },
            endVal   : {
                type    : [Number, String],
                required: true
            },
            // number of decimal places in number
            decimals : {
                type   : Number,
                default: 0
            },
            // duration in seconds
            duration : {
                type   : Number,
                default: 2
            },
            options  : {
                type: Object,
                default() {
                    return {}
                }
            },
            roundsOff: {
                type   : Boolean,
                default: true
            }
        },
        render(h) {
            return h(this.tag, {}, [this.startVal])
        },
        methods:{
            reset(){
                this._countup.reset()
            }
        },
        watch: {
            start(val) {
                if (val) {
                    this._countup.start()
                }
            },
            endVal(val) {
                this._countup.duration = this.duration*1000||2000
                this._countup.frameVal = this.startVal
                this.$nextTick(()=>{
                  this._countup.update(val)
                })
            },
            decimals(val) {
                this._countup.decimals = Math.max(0, val || 0);
                this._countup.dec = Math.pow(10, val);
            }
        }
    }
</script>
